<template>
    <section>
        <loader :is-visible="isLoading"></loader>        
        <div id="ExpenseDetail">
            <div class="cont_99P marginbottom5 form-group pt-3 px-1">
                <select v-model="detailsbyday" class="form-control width_48 col-md-6 px-1 float-left mb-2" @change="GetTotalExpense(detailsbyday,detailsbyStatusExpense)">
                    <option value="today">{{$t('Today')}}</option>
                    <option value="yesterday">{{$t('Yesterday')}}</option>
                    <option value="thisweek">{{$t('ThisWeek')}}</option>
                    <option value="last7days">{{ $t('Last7days') }}</option>
                    <option value="thismonth">{{ $t('ThisMonth') }}</option>
                    <option value="last30days">{{ $t('Last30days') }}</option>
                    <option value="lastmonth">{{$t('LastMonth')}}</option>
                </select>
                <select v-model="detailsbyStatusExpense" class="width_48 form-control pullright arabicpulleft col-md-6 px-1 float-left mb-2"  @change="GetTotalExpense(detailsbyday,detailsbyStatusExpense)">
                    <option value="Approved">{{ $t('Approved') }}</option>
                    <option value="Pending">{{ $t('Pending') }}</option>
                </select>
            </div>
            <div id="Projects" class="dashboard cont_99P px-1" >
                <ul class="newsep">
                    <li v-if="approvalTypeForTime=='Yes'" class="list-group-item d-flex justify-content-between">
                        <span class="timelabel">Amount</span><span id="lblExpenseAmount" class="timelabelright arabicpulleft">{{ TOTALEXPENSE }}.00</span>
                    </li>
                    <li v-else class="list-group-item d-flex justify-content-between">
                        <span class="timelabel">Amount</span><span id="lblExpenseAmount" class="timelabelright arabicpulleft">{{TOTALEXPENSE}}.00</span>
                    </li>
                </ul>
            </div>
        </div>
    </section>
</template>
<script>
import DataService from '../../../services/DataService';
export default {
    props: {
        widgetInfo: null,
        moduleName: null,
        fromDate: null,
        toDate: null
    },
    data(){        
        return{
            isLoading:false,
            approvalTypeForTime:'',
            TOTALEXPENSE:'',
            detailsbyday:'last7days',
            detailsbyStatusExpense:'Approved'
        }
    },
    created:function(){
        this.ExpenseDetail();
        this.GetTotalExpense(this.detailsbyday,this.detailsbyStatusExpense);
    },
    methods:{
        ExpenseDetail:function(){            
            var url=`requestType=&statusType=`
            DataService.ExpenseDetail(url).then(response=>{  
                if(response.data.length>0){
                    this.TOTALEXPENSE=response.data[0].TOTALEXPENSE;
                    this.GetConfigurationValueKey('PMCanSeeProjectFinance');
                } 
            })
        },
        GetConfigurationValueKey: function (ths) {            
            var url = `ConfigurationValueByKey=${ths}`;
            DataService.GetConfigurationValue(url).then(response => {                   
                if(response.data != null){
                    this.approvalTypeForTime = response.data.CONFIG_DATA_VALUE;
                }  
            });
        },
        GetTotalExpense:function(ths,Value){ 
           // debugger
            this.isLoading=true;
            var url=`requestType=${ths}&statusType=${Value}`
            DataService.ExpenseDetailByDate(url).then(response=>{ 
               // debugger
                if(response.data.length>0){
                    this.TOTALEXPENSE=response.data[0].TOTALEXPENSE;
                }                
                this.isLoading=false;
            });
        }
    }
}
</script>