<template>
    <div class="col-12 border p-0" style="overflow-x:auto;">

<div class="row bg-lgray  m-0 py-3 px-0 container-fluid" id="KanbanDiv" v-bind:style="{'width': ViewType == 'S' ? '100%' : '@width'}" v-cloak>
        <div v-if="moduleName=='Project'" class="col-12 pl-0 mt-2 px-small-0" style=" margin-bottom:20px !important;margin-top:5px!important;">
            <div class="input-group eamv taskclass col-sm-12 col-md-12 col-lg-8 float-left pr-1 pl-0 px-small-0">
                <div class="form-control p-0 mb-1" style="z-index:999">
                    <vue-multiselect v-model="SelectedProjects"
                                     v-bind:options="ProjectList"
                                     v-bind:multiple="true"
                                     :close-on-select="false"
                                     track-by="ProjectId"
                                     label="ProjectName"
                                     placeholder="Select project(s)"
                                     v-bind:limit="1"
                                     append-to-body="appendToBody">
                    </vue-multiselect>
                </div>
            </div>            
            <div class="col-sm-12 col-md-12 col-lg-3 float-left pl-0 px-small-0">
                <div class="search-btm-btn background-0 text-left form-check-inline d-flex">
                    <a href="javascript:;" id="aSearch" title="" class="btn btn-success mr-1" data-toggle-tooltip="tooltip" data-original-title="Search" v-on:click="filterByProject()"><i class="fa fa-search"></i></a>
                    <a href="javascript:;" id="refreshToDoList" title="" v-on:click="resetProjectFilter()" class="btn btn-danger clearsearchtext" data-toggle-tooltip="tooltip" data-original-title="Search"><i class="fa fa-refresh"></i></a>
                </div>
            </div>
        </div>
    <div class="clear"></div>
    <div class="col-12 p-0 only2-display d-flex">
         <div v-for="(parentItem,parentIndex) in DataArray" :key="parentIndex" class="col-md-12 col-lg-6"   v-bind:class="{'extended' : ViewType === 'L'}" > <!--v-show="parentItem.show==1" -->
            <ul class="combo-canvan-view">
                <li v-bind:class="parentItem.className">
                    <h3 class="mb-2 text-capitalize">
                        {{GetLocalizedValue(parentItem.name)}} ({{parentItem.count}})<span class="shorting cursor-pointer" v-on:click="sortedArray(parentItem,parentIndex)">                           
                            <i v-bind:class="'fa '+parentItem.SortIcon" aria-hidden="true"></i>
                        </span>
                    </h3>
                    <div class="clearfix"></div>
                    <div class="cards-content" v-if="parentItem.data.length!=0" ref="scrollbar" v-bind:class="{'scrollbarminiunassigned' :parentItem.className == 'unassigned','scrollbarmini' :parentItem.className != 'unassigned' }" v-bind:data-statusid="parentItem.StatusId">

                        <div class="clearfix"></div>
                        <draggable v-bind:list="parentItem.data" class="list-group" v-bind:options="{group:{ name:'grpdashboard', put: true },sort:false}" v-bind:status="parentItem.name" v-on:start="onStart" v-on:end="onEnd" v-on:change="ChangeStatusDrag($event,parentItem.StatusId)">

                            <div class="w-100 p-0" v-for="(item,index) in parentItem.data" :key="index">
                                <div class="card card-exp">
                                    <div class="card-header">
                                        <h4>
                                            <span v-bind:title="item.TASK_NAME" class="text-capitalize text-primary">{{item.TASK_NAME}}</span>
                                            <a class="nostylelink mr-3 float-right" data-toggle-tooltip="tooltip" title="@Resources.View" href="javascript:;" v-on:click="ProjectQuickView(item.PROJECT_ID,item.TASK_ID,item)" v-bind:value="item.PROJECT_ID"><i class="float-right fa fa-eye" aria-hidden="true"></i></a>
                                        </h4>
                                    </div>
                                    <div class="card-body">
                                        <div class="spac-eq text-capitalize text-dark"><b><i class="fa fa-folder-o"></i> 
                                           &nbsp; <span>{{(item.PROJECT_NAME=='CRM_LEADS'?'CRM Lead':item.PROJECT_NAME=='CRM_DEALS'?'CRM Deal':item.PROJECT_NAME=='CRM_SALES_ORDERS'?'CRM SALES ORDER':item.PROJECT_NAME=='CRM_QUOTES'?'CRM QUOTES':item.PROJECT_NAME)}}</span></b></div>
                                        <div class="spac-eq" v-if="item.TASK_DESCRIPTION != null && item.TASK_DESCRIPTION != ''"> <i class="fa fa-file-text float-left pr-1 mt-1"></i> &nbsp;<span v-html="TruncateStr(item.TASK_DESCRIPTION,200)"></span></div>
                                        <div class="spac-eq" v-else> <i class="fa fa-file-text "></i> -</div>
                                        <div class="spac-eq">
                                            <i class="fa fa-calendar "></i> &nbsp; {{item.START_DATE | formatDate}} <strong>To</strong> {{item.END_DATE | formatDate}}                                           
                                                <a v-if="moduleName =='Project' || moduleName == 'Ticketing' || moduleName == 'Event'" href="javascript:;" 
                                                v-bind:class="{'text-dark float-right': true, 'disabled': item.STATUS == 'Completed'}" v-on:click="openNoteListPopUp(item,index)">
                                                <i aria-hidden="true" class="fa fa-file-text-o actionicon_20" style="margin-right:5px;"></i>
                                                <span class="noti-circle noti-orange">{{item.NOTES_COUNT}}</span></a>                                         
                                        </div>
                                        <div class="spac-eq">
                                            <span><i class="fa fa-clock-o"></i> &nbsp;<span v-if="item.TRACKED_HOURS>0" class="blue-time pl-1">{{convertSecondsToHHMMSSNew(item.TRACKED_HOURS)}} {{$t('of')}}</span><span class="blue-time pl-1">{{item.ALLOCATE_HOURS}} {{$t('HrsSmall')}}</span></span>                                        
                                                <a v-if="moduleName == 'Project' || moduleName == 'Event'" href="javascript:;" class="text-dark float-right" v-on:click="ProjectQuickView(item.PROJECT_ID,item.TASK_ID,item,'attachment')"><i aria-hidden="true" class="fa fa-paperclip actionicon_20" style="margin-right:5px;"></i><span class="noti-circle noti-blue">{{item.FILE_COUNT}}</span></a>                                          
                                        </div>
                                        <div class="spac-eq text-capitalize">
                                            <b><i class="fa fa-user"></i> &nbsp; <span>{{item.NAME}}</span></b>
                                                <a v-if="(moduleName == 'Project' || moduleName == 'Event') && item.STATUS != 'Unassigned'" href="javascript:;" class="text-dark float-right" v-on:click="OpenCommentModel(item)"><i aria-hidden="true" class="fa fa-commenting-o actionicon_20"></i><span class="noti-circle noti-green">{{item.COMMENT_COUNT}}</span></a>
                                                <a v-else-if="(moduleName == 'Project' || moduleName == 'Event')" href="javascript:;" class="text-dark float-right" v-on:click="TeamTimeAllocation(item)"><i aria-hidden="true" class="fa fa-user-plus actionicon_20"></i></a>                                          
                                                <a href="javascript:;" v-if="moduleName == 'Ticketing' && item.STATUS == 'Unassigned'" class="text-dark float-right" v-on:click="TicketAsssignTrasfer(item.PROJECT_ID,item.TASK_ID,item)"><i aria-hidden="true" class="fa fa-user-plus actionicon_20"></i></a>                                           
                                        </div>
                                            <div v-if="moduleName != 'Ticketing'" class="spac-eq text-capitalize">
                                                <b><i class="fa fa-user-circle"></i> &nbsp; <a href="javascript:;" class="text-capitalize text-primary" v-on:click="ProjectQuickView(item.PROJECT_ID,item.TASK_ID,item,'resources')">View Assigned Resources</a></b>
                                            </div>                                                                             
                                            <div class="spac-eq" v-if="moduleName != 'HR' && TagData.filter(v => v.TASK_ID == item.TASK_ID) != null && TagData.filter(v => v.TASK_ID == item.TASK_ID).length>0">
                                                <i class="fa fa-tags" aria-hidden="true"></i> &nbsp;
                                                <span class="tags-dashbm" v-for="(itemInner,itemIndex) in TagData.filter(v => v.TASK_ID == item.TASK_ID)" :key="itemIndex">
                                                    {{itemInner.TAG_NAME}}
                                                </span>
                                            </div>
                                            <div class="spac-eq" v-else><i class="fa fa-tags" aria-hidden="true"></i> &nbsp; - </div>
                                        

                                        <div class="spac-eq" v-if="item.PRIORITY_NAME != '' && item.PRIORITY_NAME != 'null' && item.PRIORITY_NAME != null">

                                            <span class="badge" v-bind:style="getPriorityStyles(item.PRIORITY_NAME.toLowerCase(),item.COLOR_CODE)">{{item.PRIORITY_NAME}}</span>
                                        </div>
                                        <div class="spac-eq border-top text-center">
                                            <div class="time">
                                                <div v-bind:class="'c100 small p'+ parseInt(showTrackPercent(item),10)">
                                                    <span>{{showTrackPercent(item)}}%</span>
                                                    <div class="slice">
                                                        <div v-bind:class="{'bar':showTrackPercent(item) > 0}"></div>
                                                        <div class="fill"></div>
                                                    </div>
                                                </div>
                                                <span class="t-title">{{$t('Time Left')}}</span>
                                            </div>
                                            <div v-bind:class="(String(item.STATUS).toLowerCase()=='completed' ?'Task disabled_icon':'Task')">
                                                <div v-bind:class="'cursor-pointer c100 small green p'+ parseInt(item.PROGRESS,10)" v-on:click="ProgressDivOpen(item)" v-if="item.PROJECTTASKPROGRESS=='MANUALLY_BASED'">
                                                    <span>{{item.PROGRESS}}%</span>
                                                    <div class="slice">
                                                        <div v-bind:class="{'bar':item.PROGRESS > 0}"></div>
                                                        <div class="fill"></div>
                                                    </div>
                                                </div>
                                                <div v-bind:class="'c100 small green p'+ parseInt(item.PROGRESS,10)" v-if="item.PROJECTTASKPROGRESS!='MANUALLY_BASED'">
                                                    <span>{{item.PROGRESS}}%</span>
                                                    <div class="slice">
                                                        <div v-bind:class="{'bar':item.PROGRESS > 0}"></div>
                                                        <div class="fill"></div>
                                                    </div>
                                                </div>
                                                <span class="t-title"> {{$t('Task')}} {{$t('Progress')}}</span>
                                            </div>
                                        </div>
                                        <div class="spac-eq border-top text-center">
                                            <div id="TotalTime" data-model="item.TotalTime" v-if="item.AutomaticBtn" class="timerbx green"></div>
                                            <button v-if="item.RUNNING_STATUS=='ACT'"
                                             v-bind:class="{'inactive-disabled':item.TIME_TRACKED_BY==ID}" 
                                             v-on:click="PauseResumeTime('pause',TRACK_ID,COMPANY_ID,CREATED_BY,item.TASK_ID,item.PROJECT_ID,item.IS_BILLABLE,item)" type="button" class="btn Timertodo btn-info">
                                                <span class="tag-name"> {{$t('Pause')}}</span>
                                            </button>
                                            <button v-if="item.RUNNING_STATUS=='PAUS'" v-bind:class="{'inactive-disabled':ID}" 
                                            v-on:click="PauseResumeTime('resume',TRACK_ID,COMPANY_ID,CREATED_BY,item.TASK_ID,item.PROJECT_ID,item.IS_BILLABLE,item)" type="button" class="btn Timer btn-success">
                                                <span class="tag-name"> {{$t('Resume')}}</span>
                                            </button>
                                            <button v-if="item.RUNNING_STATUS=='PAUS' || item.RUNNING_STATUS=='ACT'" v-bind:class="{'inactive-disabled':item.TIME_TRACKED_BY==ID}" 
                                            v-on:click="StopTime(item.IS_BILLABLE,item.RUNNING_STATUS_ID,item.TASK_ID,item,COMPANY_ID,CREATED_BY,TRACK_ID)" type="button" class="btn Timer btn-danger StopTime">
                                                <span class="tag-name">{{$t('Stop')}}</span>
                                            </button>
                                            <button type="button" class ="d-none" v-bind:class="(String(item.STATUS).toLowerCase()=='completed' || String(item.STATUS_NAME).toLowerCase() == 'closed'?'btn Timer manually-time inactive-disabled':'btn Timer manually-time')" v-on:click="MannualDiv(item)" v-if="item.RUNNING_STATUS !='PAUS' && item.RUNNING_STATUS != 'ACT'" data-toggle="modal" href="javascript:;">
                                                <span class="tag-name"><i class="fa fa-clock-o mr-1"></i>{{$t('Add')}}{{$t('Mannualy')}}</span>
                                            </button>
                                            <button type="button"  class ="d-none" v-bind:class="(String(item.STATUS).toLowerCase()=='completed' || String(item.STATUS_NAME).toLowerCase() == 'closed'?'btn Timer auto-time inactive-disabled':'btn Timer auto-time')" v-if="item.RUNNING_STATUS !='PAUS' && item.RUNNING_STATUS != 'ACT'" v-on:click="StartTime(item.IS_BILLABLE,item.RUNNING_STATUS_ID,item.TASK_ID,item)" data-toggle="modal" href="javascript:;">
                                                <span class="tag-name"><i class="fa fa-history mr-1"></i>{{$t('Automatic')}}c</span>
                                            </button>
                                        </div>
                                        <div class="verticle-action mt-1">
                                            <i class="fa fa-ellipsis-v action_icon_cv" aria-hidden="true" style="width:10px;"></i>
                                            <span class="action-list-box small">
                                                <span class="list-actions fsm-list" id="action-list">
                                                    <div class="float-left">
                                                        <div class="float-left">                              
                                                                <a v-if="moduleName == 'Project' && String(item.STATUS).toLowerCase()!='completed'" title="@Resources.Request @Resources.Time" data-placement="left" class="update actions-onclick" v-on:click="RequestTimeBtn(item)" 
                                                                v-bind:class="(item.IS_APPROVE_REJECT > 0 ?'edit-list disabled_icon':'edit-list')" href="javascript:;">
                                                                    <i class="fa fa-reply text-dark"></i>
                                                                    <span class="text"> {{$t('Request')}}{{$t('Time')}}</span>
                                                                </a>                                                            
                                                            <a :title="$t('QuickView')" data-placement="left" v-on:click="ProjectQuickView(item.PROJECT_ID,item.TASK_ID,item)" class="update actions-onclick" href="javascript:;" v-bind:attr="item.PROJECT_ID">
                                                                <i class="fa fa-eye text-info action-icon"></i>
                                                                <span class="text">{{$t('QuickView')}}</span>
                                                            </a>
                                                        </div>
                                                    </div>
                                                    <i class="fa fa-times close close-action_cv cursor-pointer" aria-hidden="true"></i>
                                                </span>
                                            </span>
                                        </div>
                                    </div>                                 
                                </div>
                            </div>
                        </draggable>
                    </div>
                    <div class="cards-content" v-if="parentItem.data.length==0" ref="scrollbar" v-bind:class="{'scrollbarminiunassigned' :parentItem.className == 'unassigned','scrollbarmini' :parentItem.className != 'unassigned' }" v-bind:data-statusid="parentItem.StatusId">
                        <div class="clearfix"></div>
                        <draggable v-bind:list="[]" class="list-group" v-bind:options="{group:{ name:'grpdashboard', put: true },sort:false}" v-bind:status="parentItem.name" v-on:start="onStart" v-on:end="onEnd" v-on:change="ChangeStatusDrag($event,parentItem.StatusId)">
                            <div class="w-100 float-left text-center border p-2"> <span class="text-center text-danger no-record">{{$t('NoRecordfound')}}</span></div>
                        </draggable>
                    </div>
                </li>
            </ul>
        </div>
      
       
    </div>
    <!-- <Modalprojectquickview v-if="ShowManageProjectQuickView" ref="foo" :checkVueodolist="this" @close="ShowManageProjectQuickView = false,modalProjectId = null,modalTaskId = null">
        <h5 slot="header"></h5>
    </Modalprojectquickview>
    <Modaladdmanually v-if="showAddManuallyModal" @close="showAddManuallyModal = false">
        <h5 slot="header">{{$t('AddManually')}}</h5>
    </Modaladdmanually>   
<modalupdateprogress v-if="showUpdateProgressModal" @close="showUpdateProgressModal = false">
    <h5 slot="header"> {{$t('Update')}} {{$t('Progress')}} </h5>
</modalupdateprogress>
<Modalrequesttime v-if="showRequestTimeModal" @close="showRequestTimeModal = false">
    <h5 slot="header">{{$t('Time')}} {{$t('Time')}}</h5>
</Modalrequesttime>
<Modalcommentlist v-if="showCommentModal" @close="showCommentModal = false, modalCommentItem = null">
    <h5 slot="header"></h5>
</Modalcommentlist>
    <modalnote v-if="showAddNoteModal" @close="showAddNoteModal = false, modalNoteItem = null" v-bind:taskid="TaskId1">
        <h5 slot="header" class="modal-title">
            {{header}}
        </h5>
    </modalnote>
    <Projecttasknote userid="@loggedUserInfo.ID" reqfrom="dashboard" v-if="showNoteModal" @close="showNoteModal = false, modalNoteTaskItem = null">
        <h5 slot="header" class="modal-title">
            {{$t('Notes')}}
        </h5>
    </Projecttasknote>
    <Modalallocationlist v-if="showAllocationModal" checkVueodolist="this" v-bind:projectid="ProjectId" @close="showAllocationModal = false,modalAddEditTaskId = null"></Modalallocationlist> -->
</div>
<div id="divuploadnewfile"></div>
<input type="hidden" id="hdnUploadNewFilePopUp" value="M" url="" />
</div>

</template>

<script>
import DataService from '../../../services/DataService';
//import Modaladdmanually from './Modal/modaladdmanually.vue';
//import Modalallocationlist from './Modal/modalallocationlist.vue';
//import Modalcommentlist from './Modal/modalcommentlist.vue';
///import Modalprojectquickview from './Modal/modalprojectquickview.vue';
//import Modalrequesttime from './Modal/modalrequesttime.vue';
//import modalupdateprogress from './Modal/modalupdateprogress.vue';
//import Projecttasknote from './Modal/projecttasknote.vue';
//import modalnote from '../../../components/Common/Modal/modalnote.vue'

export default {
   // components: { Modalprojectquickview, Modaladdmanually, modalupdateprogress, Modalrequesttime, Modalcommentlist, Projecttasknote, Modalallocationlist,modalnote },
    props: {
        moduleName: null,
        encCompanyId:null,
        encUserId:null,
        encTrackId:null
    },
    data() {
        return {
            ID: null,
            user_type: null,
            typeProject: "kanban",
            HeaderDataPending: null,
            ToDoListData: [],
            TagData: null,
            ApiURLKanban: "subModuleCode=Project_Detail&moduleName=",
            InProgressProject: 0,
            PendingProject: 0,
            activeData: [],
            pendingData: [],
            ShowManageProjectQuickView: false,
            showAddManuallyModal: false,
            showUpdateProgressModal: false,
            showRequestTimeModal: false,
            showCommentModal: false,
            sortDirectionIndex: 0,
            sortDirection: "ASC",
            HeaderDataInprogress: null,
            typeStatus: null,
            DataArray: [],
            ViewType: "S",
            SelectedItem: null,
            TRACKED_HOURS: 0,
            secondsPerMinute: 60,
            minutesPerHour: 60,
            QuickViewType: "",
            modalProjectId: null,
            modalTaskId: null,
            showNoteModal: false,
            modalNoteTaskItem: null,
        
            showAddNoteModal: false,
            modalNoteItem: null,
            header: "",
            TaskId1: null,
            typeNoteTask: "noteTask",
            ProjectId: null,
            AddNoteUserGuide: "User_Guide_Note",
            ModuleName: this.moduleName,
            DraggedFromElementStatus: "",
            DraggedToElementStatus: "",
            DraggedElementTaskId: null,
            DraggedElementStatus: null,
            showAllocationModal: false,
            modalAddEditTaskId: null,
            TaskTeam: null,
            itemHours: null,
            sDate: null,
            eDate: null,
            rowIndexNested: null,
            Team: [],
            TaskHours: null,
            CompanyId: null,
            UserId: null,
            ProjectList: [],
            SelectedProjects: "",
            SelectedProjectIds: "",
            IsViewResourcesClicked: false,
            PageNumber: 1,
            DisableTimer: "no",
            pendingId:null,
            inprogressId:null,
            completedId:null
        };
    },
    created: function () {
        //debugger
        //this.GetStatusIdByCode('REFPEN');
        //this.GetStatusIdByCode('PNPGRS');
       // this.GetStatusIdByCode('STATUS_DONE');
        this.ApiURLKanban=this.ApiURLKanban+this.moduleName;
       // this.ID = this.GetUserInfo.ID;
        this.user_type = this.GetUserInfo.UserType;
        this.CompanyId = this.GetUserInfo.CompanyId;
        this.UserId = this.GetUserInfo.ID;
        this.FetchDataKanban("kanban", null);
       // this.onStatusList();
    },
    methods: {
        GetStatusIdByCode: function (refCode) {
            var vm = this;
            DataService.GetStatusIdByCode('statusCode=' + refCode).then(function (response) {
                if (refCode == 'REFPEN') {
                    vm.pendingId = response.data;
                } else if (refCode == 'PNPGRS') {
                    vm.inprogressId = response.data;
                } else if (refCode == 'STATUS_DONE') {
                    vm.completedId = response.data;
                }
            });
        },
        OnScrollItem: function (item) {
            if (this.PageNumber <= Math.ceil(item.count / 10)) {
                this.PageNumber = this.PageNumber + 1;
                this.FetchDataKanban("kanban", null);
            }
        },
        GetPrivilege: function (param) {
            var vm = this;
            (async()=>{
          await  DataService.CheckPrivilege(param)
                .then(function (response) {
            });
        })
        },
        getPriorityStyles: function (priority, color) {
            if (color == "" || color == null || typeof (color) == "undefined") {
                color = "#25a5fc";
            }
            return {
                background: color
            };
        },
        ShowDataArray: function () {
            var vm=this;
            var array = [];
            if (this.user_type == "CA") {
                array.push({
                    name: "unassigned",
                    count: 0,
                    data: [],
                    show: 0,
                    className: "unassigned",
                    SortIcon: "fa-sort-alpha-desc",
                    StatusId: 0
                });
            }
            array.push({
                name: "overdue",
                count: 0,
                data: [],
                show: 0,
                className: "canceled",
                SortIcon: "fa-sort-alpha-desc",
                StatusId: 0
            });
            array.push({
                name: "pending",
                count: 0,
                data: [],
                show: 1,
                className: "pending",
                SortIcon: "fa-sort-alpha-desc",
                StatusId: vm.pendingId
            });
            array.push({
                name: "inprogress",
                count: 0,
                data: [],
                show: 1,
                className: "overdue",
                SortIcon: "fa-sort-alpha-desc",
                StatusId: vm.inprogressId
            });
            array.push({
                name: "completed",
                count: 0,
                data: [],
                show: 0,
                className: "upcoming",
                SortIcon: "fa-sort-alpha-desc",
                StatusId: vm.completedId
            });
            array.push({
                name: "upcoming",
                count: 0,
                data: [],
                show: 0,
                className: "inactive",
                SortIcon: "fa-sort-alpha-desc",
                StatusId: 0
            });
            this.DataArray = array;
        },
        ChangeView: function (type) {
            var vm = this;
            if (type == "T") {
                vm.ViewType = "L";
                vm.DataArray.forEach(function (item) { item.show = 1; });
            }
            else {
                vm.ViewType = "S";
                $.each(vm.DataArray, function (index, item) {
                    if (item.name == "pending" || item.name == "inprogress") {
                        item.show = 1;
                    }
                    else {
                        item.show = 0;
                    }
                });
            }
        },
        openNoteListPopUp: function (item, index) {
            var getPath = window.location.pathname;
            var getController = getPath.split("/");
            if (getController[1] == "Ticketing" && getController[2] == "Dashboard") {
                if (this.GetPrivilege('controller=Ticketing&action=NoteList')) {
                    var vm = this;
                    vm.showNoteModal = true;
                    vm.modalNoteTaskItem = item;
                    this.ProjectId = item.PROJECT_ID;
                }
            }
            else {
                var vm = this;
                vm.showNoteModal = true;
                vm.modalNoteTaskItem = item;
                this.ProjectId = item.PROJECT_ID;
            }
        },
        TeamTimeAllocation: function (item) {
            //debugger;
            var vm = this;
            vm.showAllocationModal = true;
            vm.AllocationModal = item;
            vm.modalAddEditTaskId = item.TASK_ID;
            vm.ProjectId = item.PROJECT_ID;
            vm.TaskTeam = vm.Team;
            vm.itemHours = item.ALLOCATE_HOURS;
            vm.TaskHours = item.ESTIMATED_HOURS;
            //vm.sDate = item.START_DATE;
            //vm.eDate = item.END_DATE;
            vm.sDate = moment(item.START_DATE).format("MM/DD/YYYY");
            vm.eDate = moment(item.END_DATE).format("MM/DD/YYYY");
            vm.rowIndexNested = item.ItemIndex;
        },
        openProjectNoteModel: function (item, taskid) {
            var vm=this;
            if (item != "" && item != null) {
                if (this.GetPrivilege('controller=Project&action=EditNote')) {
                    // debugger;
                    this.TaskId1 = taskid;
                    //this.ProjectId = item.PROJECT_ID;
                    this.showAddNoteModal = (this.showAddNoteModal == true) ? false : true;
                    this.modalNoteItem = item;
