        formatDate: function (value) {
            if (value) {
                try {
                    var timeFormat = 'hh:mm A';
                    timeFormat = String(timeFormat).replace("t", "A").replace("t", "");
                    var getDate = new Date(moment(String(value)).year(), moment(String(value)).month(), moment(String(value)).date(), moment(String(value)).hours(), moment(String(value)).minutes());
                    return moment(getDate, "YYYY-MM-DD HH:mm:ss").format(String('M/dd/yyyy').toUpperCase() + " " + String(timeFormat));
                }
                catch (e) {
                    var timeFormat = 'hh:mm A';
                    timeFormat = String(timeFormat).replace("t", "a").replace("t", "");
                    return moment(String(value)).format(String('M/dd/yyyy').toUpperCase() + " " + String(timeFormat));
                }
            }
        },
        convertDecimalValueInFormat: function (value) {
            try {
                value = parseFloat(value, 10);
                value = value.toFixed(2);
                value = parseFloat(value, 10);
                value = value.toLocaleString("en-US", { minimumFractionDigits: 2, maximumFractionDigits: 2 });
                return value;
            }
            catch (ex) {
                return value;
            }
        },
