            START_DATE_TIME = data.ParentStartDate;
            var now = moment(START_DATE_TIME);
            var then = moment(STOP_DATE_TIME);
            var ms = moment(then, "DD/MM/YYYY hh:mm:ss").diff(moment(now, "DD/MM/YYYY hh:mm:ss"));
            var duration = moment.duration(ms);
            var formatted = moment.utc(duration.asMilliseconds()).format("HH:mm:ss");
            // convertSecondsToHHMMSSNew(formatted)
            var TotalHours = vm.convertHHmmssToSeconds(formatted);
            TotalHours = TotalHours - parseInt(data.ResumeHours, 10);
