                TotalHours = TotalHours - parseInt(data.ResumeHours, 10);
            }
            var STOP_DATE_TIME = "";
            STOP_DATE_TIME = vm.FromJsDateTime(vm.CurrentDate("time", vm.Timer));            
            //var url = "/Timer/SaveTimeEntry";
            DataService.SaveTimeEntry({
                    trackId: TRACK_ID,
                    trackDescription: TRACK_DESCRIPTION,
                    startTime: START_DATE_TIME,
                    //startDate: StartDate,
                    stopTime: STOP_DATE_TIME,
                    //stopDate: StopDate,
                    isBillable: IS_BILLABLE,
                    companyId: COMPANY_ID,
                    createdBy: CREATED_BY,
                    trackAgent: TRACK_AGENT,
