                    totalhours: TotalHours,
                    timeformat: timeformat,
                    TagIds: "",
                    isForcedUpdate: isForcedUpdate
                }).then(function(response){
                    if (response.data.Status == "overlap") {
                        var dialog = $("#alert");
                        dialog.html("<div class=\"grapghloader\">&nbsp; </div>");
