                    }
                })            
        },
        FromJsDateTime: function (vDateTime) {
            return Globalize.format(vDateTime, 'M/dd/yyyy' + " " + 'hh:mm A');
        },
        convertHHmmssToSeconds: function (hhmmss) {
            var totaltime = hhmmss.split(":");
            var hours = parseInt(totaltime[0], 10) * 60 * 60;
            var minutes = parseInt(totaltime[1], 10) * 60;
            var seconds = parseInt(totaltime[2], 10);
            var convertedSeconds = hours + minutes + seconds;
            return convertedSeconds;
        },
