                        return element.STATUS_CODE == "PNPGRS";
                    });
                    vm.pendingData = $.grep(vm.StatusList, function (element, index) {
                        return element.STATUS_CODE == "REFPEN";
                    });
                }
            });
        },
        FetchDataKanban: function (type, userIds) {
            var vm = this;
            vm.isLoading=true;
            var urlMain = location.href;
            if (typeof userIds == "undefined") {
                userIds = null;
            }
            var isPartial = 0;
            if (vm.PageNumber > 1) {
                isPartial = 1;
            }
            var params =vm.ApiURLKanban+ "&pageSize=10&pageNumber=" + this.PageNumber + "&sortBy=&sortExp=&searchCondition=&projectId=" + vm.SelectedProjectIds + "&type=" + type + "&userIds=" + userIds + "&isPartial=" + isPartial;
            DataService.ToDoListDashboard(params).then(function (response) {
                
                vm.isLoading=false;
                vm.HeaderDataPending = [];
                vm.HeaderDataInprogress = [];
                var responseData = response.data.result.DATA;
                if (isPartial == 0) {
                    vm.ShowDataArray();
                }
                if (vm.ViewType == "L") {
                    $.each(vm.DataArray, function (index, item) {
                        if (item.name == "unassigned" && userIds != null) {
                            item.show = 0;
                        }
                        else {
                            item.show = 1;
                        }
                    });
                }
                else {
                    $.each(vm.DataArray, function (index, item) {
                        if (item.name == "pending" || item.name == "inprogress") {
                            item.show = 1;
                        }
                        else {
                            item.show = 0;
                        }
                    });
                }
                if (responseData != null && responseData.length > 0) {
                    if (typeof responseData != "undefined") {
                        if (isPartial == 0) {
                            vm.ToDoListData = responseData;
                            $.each(vm.DataArray, function (index, item) {
                                var name = item.name;
                                var data = $.grep(responseData, function (element, index) {
                                    return element.STATUS.toLowerCase() == name;
                                });
                                var getData = $.grep(vm.DataArray, function (element, indexs) {
                                    return element.name == name;
                                });
                                getData[0].count = data.length;
                                if (name == "unassigned" && data != null && typeof (data[0]) != "undefined") {
                                    getData[0].count = data[0].count_data;
                                }
                                getData[0].data = data;
                                if (typeof (data[0]) != "undefined") {
                                    getData[0].StatusId = data[0].STATUS_ID;
                                }
                            });
                        }
                        else {
                            var name = "unassigned";
                            //debugger;
                            var data = responseData;
                            //debugger;
                            var getData = $.grep(vm.DataArray, function (element, indexs) {
                                return element.name == name;
                            });
                            //getData[0].count = data.length;
                            if (data.length > 0) {
                                getData[0].data = getData[0].data.concat(data);
                                getData[0].count = data[0].count_data;
                            }
                            if (typeof (data[0]) != "undefined") {
                                getData[0].StatusId = data[0].STATUS_ID;
                            }
                        }
                        vm.TagData = response.data.result.TAGDATA;
                        vm.PendingProject = response.data.result.DATA[0].PENDINGRECORDS;
                        vm.InProgressProject = response.data.result.DATA[0].PROGRESSRECORDS;
                    }
                    else {
                        vm.HeaderDataPending = [];
                        vm.HeaderDataInprogress = [];
                        vm.InProgressProject = 0;
                        vm.PendingProject = 0;
                    }
                }
                else {
                    vm.HeaderDataPending = [];
                    vm.HeaderDataInprogress = [];
                    vm.InProgressProject = 0;
                    vm.PendingProject = 0;
                }
                vm.SelectedProjectIds = "";
                setTimeout(function () {
                    $(".scrollbarminiunassigned").scroll(function (e) {
                        e.stopImmediatePropagation();
                        console.log(parseInt($(".scrollbarminiunassigned").scrollTop(), 10) > parseInt(($(".scrollbarminiunassigned .list-group").height() - $(window).height()), 10) - 100);
                        if (parseInt($(".scrollbarminiunassigned").scrollTop(), 10) > parseInt(($(".scrollbarminiunassigned .list-group").height() - $(window).height()), 10) - 100) {
                            vm.PageNumber = vm.PageNumber + 1;
                            vm.FetchDataKanban("kanban", null);
                        }
                    });
                }, 1000);
            });
        },
        filterByProject: function () {
            var vm = this;
            vm.SelectedProjectIds = "";
            if (vm.SelectedProjects.length > 0) {
                vm.SelectedProjectIds = vm.SelectedProjects.map(x => x.ProjectId).join(",");
            }
            vm.FetchDataKanban("kanban", checkVueList.users);
        },
        resetProjectFilter: function () {
            var vm = this;
            vm.SelectedProjects = [];
            vm.SelectedProjectIds = "";
            vm.FetchDataKanban("kanban", checkVueList.users);
        },
        refreshProjectDropDown: function () {
            var vm = this;
            if (!checkVueList.users) {
                vm.ProjectList = JSON.parse("@Html.Raw(projectList)");
            }
            else {
                DataService.ProjectListDashboard('?userIds='+ checkVueList.users)
                .then(function (response) {
                    if (response.data.status == "Success") {
                        vm.ProjectList = response.data.result.DATA;
                    }
                });
            }
        }
    },    
}
</script>


