<template>
    <div :id="'pieDiv_' + moduleName + '_' + widgetInfo.action"
        class="w-100 bg-white custom-chart-div t-min-height piechrt text-center">
        <div v-bind:class="{ 'd-inline-block mt-2 reportBlock  align-center c3 w-60': PieWidgetData.length > 0, 'd-inline-block mt-2 align-center c3 w-60': PieWidgetData.length == 0 }"
            :controller="moduleName" :id="'div_WidgetChart_' + widgetInfo.action"
            v-bind:style="{ 'width:60%; float:left; text-align:center;': PieWidgetData.length > 0, 'width:100%;text-align:center;': PieWidgetData.length == 0 }">
            <span class="text-danger no-record">{{ $t('NoRecordfound') }}</span>
        </div>
        <div :id="'chartInTransitAssetListLegend_' + widgetInfo.action" class="chartInTransitAssetListLegend justify-content-left align-self-center w-40 c3"></div>
    </div>
</template>

<script>
import DataService from '../../../services/DataService';

export default {
    props: {
        widgetInfo: null,
        moduleName: null,
        fromDate: null,
        toDate: null
    },
    data() {
        return {
            PieWidgetData: [],
            HeaderData: null,
            showAddDashboardReport: false,
        }
    },
    created: async function () {
        await this.FetchData();
    },
    methods: {
        FetchData: async function () {
          //  debugger
            var vueObj = this;
            var fromDate = vueObj.fromDate != null ? vueObj.fromDate : vueObj.$options.filters.formatDate(new Date());
            var toDate = vueObj.toDate != null ? vueObj.toDate : vueObj.$options.filters.formatDate(new Date());
            var params = `reportType=${vueObj.widgetInfo.action}&fromDate=${fromDate}&toDate=${toDate}`;
            await DataService.GetDashboardData(params).then(function (response) {
                debugger
                if (response.data != null && response.data != "") {
                    vueObj.PieWidgetData = response.data;
                    if (response.data.status == "Success") {
                        if (vueObj.widgetInfo.charT_TYPE == "table" && response.data.result.length>0) {
                            vueObj.HeaderData = response.data.result.headers;
                        }
                        if (response.data.result.length>0) {
                            vueObj.PieWidgetData = response.data.result;
                            vueObj.bindWidget(vueObj,200,200);
                        }
                    }
                }
            });
        },
        ResizeReportWidget: function (zoom, reportName) {
            var name = '#div_BarChart_' + reportName;
            var chart = $(name).data('c3-chart');
            chart.revert();
            if (zoom == "1") {
                chart.resize({ height: 200, width: 400 })
            }
            else {
                if ($(window).width() <= 1280 && $(window).height() <= 1024) {
                    chart.resize({ height: 200, width: 300 })
                }
                else {
                    chart.resize({ height: 300, width: 800 })
                }
            }
        },
        bindWidget: function (vueObj,width,hight) {
         //   debugger
            if (vueObj.widgetInfo.charT_TYPE == "pie") {             
                var arrAU = [];
                var chartType = "pie";
                var chartId='#chartInTransitAssetListLegend_'+vueObj.widgetInfo.action;
                $(chartId).empty();
                $.each(vueObj.PieWidgetData, function (index, element) {
                    arrAU.push([element.name, element.value]);
                });
                let selector = '#div_WidgetChart_' + vueObj.widgetInfo.action;
                let chartInTransitAssetListWise = c3.generate({
                    bindto: selector,
                    size: {
                        width: width,
                        height: hight,
                    },
                    data: {
                        columns: arrAU,
                        type: 'pie',
                        onmouseover: function (d, i) {
                            $("div "+chartId+" .legend").find("div").addClass("c3-legend-item-hidden");
                            $("div "+chartId+"  .legend").find("div[data-id='" + d.id + "']").removeClass("c3-legend-item-hidden");
                        },
                        onmouseout: function (d, i) {
                            $("div "+chartId+"  .legend").find("div").removeClass("c3-legend-item-hidden");
                        },
                        empty: {
                            label: {
                                position: "right",
                                text: vueObj.$t("NoRecordfound")
                            }
                        },
                    },
                    legend: {
                        show: false
                    }
                    ,color: { pattern: ["#23A4FF", "#FF6B6B", "#5FB49C", "#776274", "#916953", "#28587B", "#F4B701", "#272635", "#AEE5D8", "#FFC482", "#3C6E71", "#9FA0FF", "#D1B1CB", "#95B2B8", "#EF8354", "#72D9C8", "#C6EBBE", "#56203D", "#A09CB0", "#5C573E"] },
                });
                d3.select(chartId).insert('div', '.chart').attr('class', 'legend text-left').selectAll('div')
                    .data(arrAU)
                    .enter().append('div')
                    .attr('data-id', function (id) {
                        return id;
                    })
                    .html(function (id) {
                        return '<span class="text-truncate w-80 namespan">' + id + '</span>';
                    })
                    .each(function (id) {

                        d3.select(this).append('span').style('background-color', chartInTransitAssetListWise.color(id[0]));
                    })
                    .on('mouseover', function (id) {
                        chartInTransitAssetListWise.focus(id);
                    })
                    .on('mouseout', function (id) {
                        chartInTransitAssetListWise.revert();
                    })
                    .on('click', function (id) {
                        $(this).toggleClass("c3-legend-item-hidden")
                        chartInTransitAssetListWise.toggle(id);
                    });

                    }
                },
    }
}
</script>
<style type="text/css">
#chartInTransitAssetList,
.chartInTransitAssetListLegend {
    float: left;
}

.legend span {
    display: inline-block;
    margin-left: 7px;
    margin-right: 7px;
    padding: 5px;
    margin-top: 5px;
}

.chartInTransitAssetListLegend .legned div {
    width: 100%;
}

.chartInTransitAssetListLegend .legend {
    max-height: 328px;
    overflow-y: auto;
}

.legend span {
    display: inline-block;
    margin-left: 7px !important;
    margin-right: 0px !important;
    padding: 5px !important;
    margin-top: 7px !important;
    float: left !important;
    width: 10px !important;
}

    .legend span.namespan {
        width: 66% !important;
        float: right !important;
        text-align: left;
        margin: 0px !important;
        padding: 0px !important;
        margin-right: 15px !important;
    }
</style>