<template>
    <section>
        <section class="page-action">
            <div class="row">
                <input type="hidden" :value="titleName" id="hdnModuleName" />
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition partition-full p-actions-expand before-d-none overflow-visible">
                        <span class="p-name text-white">{{$t('ExpenseDashboard')}}</span>
                        <span class="p-actions  float-right">
                            <a href="javascript:;" class="p-action-btn text-white link" @click="MyViews()"><em
                                    class="fa fa-user" aria-hidden="true"></em><br>{{$t('MyView')}}</a>
                            <a href="javascript:;" class="p-action-btn text-white" id="teamview" @click="TeamView()"><em
                                    class="fa fa-users" aria-hidden="true"></em><br>{{ $t('TeamView') }}</a>
                            <a class="p-action-btn text-white d-none" id="ancChangeView" data-isshow="0"><em
                                    class="fa fa-pencil" aria-hidden="true"></em><br>{{$t('ChangeView')}}</a>
                            <div class="dropdown p-action-btn text-white" id="ddlChangeView">
                                <a class="text-white dropdown-toggle d-inline-block" id="dropdownMenuButton"
                                    data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    <em class="fa fa-eye" aria-hidden="true"></em><br />
                                    <span class="spnChangeView">{{ $t('SelectView') }}</span>
                                </a>
                                <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                                    <a class="dropdown-item" data-value="B" href="javascript:;"
                                        @click="ddlChangeView('B')">{{ $t('ALL') }}</a>
                                    <a class="dropdown-item" data-value="R" href="javascript:;"
                                        @click="ddlChangeView('R')">{{$t('Reports')}}</a>
                                    <a class="dropdown-item" data-value="T" href="javascript:;"
                                        @click="ddlChangeView('T')">{{ $t('ToDoList') }}</a>
                                </div>
                            </div>
                        </span>
                        <span class="collapse-head-btns">
                            <a href="javascript:;" class="collapse-btns"><i class="fa fa-angle-up text-white"></i></a>
                        </span>
                    </div>
                </div>
            </div>
        </section>
        <section class="main-content nd-combo-view">
            <div id="parentPageDiv" class="padding-t_10">
                <div class="col-md-12 p-0">
                    <CountList :moduleName="moduleName" ref="countList"></CountList>
                    <div class="col-lg-12 p-0" id="divToDoListSort">
                        <div class="row mt-2">
                            <div class="col-md-5" id="divToDoList">
                                <div class="heading-border" style="z-index:9;">
                                    <h5 class="h5"><span>Expense To DoList</span></h5>
                                </div>
                                <div id="toDoListing">
                                    <ToDoList v-if="encCompanyId" :moduleName="moduleName" :encCompanyId="encCompanyId"
                                        :encUserId="encUserId" :encTrackId="encTrackId"
                                        @fetch-count-list="FetchCountListData" ref="todolist"></ToDoList>
                                </div>
                            </div>
                            <div class="col-md-7 mt-4 mt-lg-0" id="divReportsdata">
                                <div class="heading-border">
                                    <h5 class="h5"><span>{{ $t('ExpenseReports') }}</span></h5>
                                </div>
                                <div class="nd-repoort-filter">
                                    <span class="p-actions float-right">
                                        <DateRange pgaction="global" @set-date="SetDateRange"></DateRange>
                                    </span>
                                </div>
                                <div class="dashbord-graph-widget row bg-lgray border m-0 py-3 with-combo scrollbarmini"
                                    id="Reports">
                                    <WidgetReport v-if="widgets.length > 0" ref="widget" :widgets="widgets"
                                        :moduleName="moduleName" :viewType="viewType" :fromDate="fromDate" :toDate="toDate"
                                        @reload-report="GetReports">
                                    </WidgetReport>
                                </div>
                            </div>
                            <div class="col-md-12 mt-3" id="divAddMoreReports">
                                <div class="text-right mt-1">
                                    <a href="javascript:;" class="btn btn-success" @click="OnAddMore">
                                        <i class="fa fa-plus pr-2"></i>{{ $t('AddMore') }}</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </section>
</template>
<script>
import DataService from "../../../services/DataService"
import WidgetReport from './WidgetReport.vue';
import DateRange from './DateRange.vue';
import CountList from './CountList.vue';
import ToDoList from './ToDoList.vue'
export default {
    components: { DateRange, WidgetReport, CountList, ToDoList },
    data() {
        return {
            titleName: '',
            moduleName: 'Expenses',
            widgets: [],
            viewType: 'R',
            fromDate: null,
            toDate: null,
            MenuButton: false,
            toDos: [],
            encCompanyId: null,
            encUserId: null,
            encTrackId: null,
            fromDt: null,
            toDt: null,
            IsReport: false
        }
    },
    created: function () {
        this.ClickTimer('Month', null, 'global')
        this.GetReports();
    },
    methods: {
        OnAddMore() {
            this.$refs.widget.$data.showReports = true;
        },
        CloseReport() { this.IsReport = false; },
        FetchCountListData(type) {
            this.$refs.countList.$data.FetchData(type, this.$refs.countList.$data.users);
        },
        GetStartEndDates(dateSpan, e, action) {
            var vm = this;
            vm.isLoading = true;
            var params = `DateSpan=${dateSpan.toLowerCase()}&report=${$('#hdnGroupBy').val()}`;
            DataService.GetStartEndDate_New(params).then(function (response) {
                vm.isLoading = false;
                var data = response.data;
                vm.fromDate = data.dateStart;
                vm.toDate = data.dateEnd;
                var fdt = data.dateStart;
                var tdt = data.dateEnd;
                $('#txtFromDate_' + action).val(data.dateStart);
                $('#txtToDate_' + action).val(data.dateEnd);
                $('#aDateLable_' + action).find('em').text(data.displayText);
                vm.$emit('set-date', data.dateStart, data.dateEnd);
                if (action != 'global') {
                    setTimeout(() => {
                        vm.FetchDataSingle(action, fdt, tdt);
                    }, 500);
                }
            });
        },
        ClickTimer(type, e, action) {
            var vm = this;
            $("#hdnViewId").val($(e).parent().attr("id"));
            if ($("#hdnViewId").val() == $("#hdnPinnedViewId").val()) {
                $("#idPinView").closest(".btnPin").addClass("pinmyview");
                $("#idPinView").closest(".btnPin").removeClass("unpinmyview");
            }
            else {
                $("#idPinView").closest(".btnPin").addClass("unpinmyview");
                $("#idPinView").closest(".btnPin").removeClass("pinmyview");
            }
            $(".btnPin").show();
            if ($(e).closest("div.DatePopUp").attr("data-pageaction") == "global") {
                $(e).closest('.date_range').find('#aDateLable_global').find('em.text-white').html('').html($(e).text());
                vm.isLoading = true;
                $("#dashbord-graph-widget").find(".ticketingDashboardHead").each(function () {
                    $(this).find(".DatePopUp").find("a[class=" + type + "]").trigger("click");
                    $(this).find(".DatePopUp").hide();
                });
                if (type == "Today") {
                    vm.GetStartEndDates(type, e, action);
                }
                else if (type == "Yesterday") {
                    vm.GetStartEndDates("yesterday", e, action);
                }
                else if (type == "Week") {
                    vm.GetStartEndDates("thisweek", e, action);
                }
                else if (type == "LastWeek") {
                    vm.GetStartEndDates("lastweek", e, action);
                }
                else if (type == "Month") {
                    vm.GetStartEndDates("thismonth", e, action);
                }
                else if (type == "LastMonth") {
                    vm.GetStartEndDates("lastmonth", e, action);
                }
                else if (type == "AllTime") {
                    vm.GetStartEndDates("thisyear", e, action);
                }
                vm.isLoading = false;
            }
            else {
                if (type == "Today") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("day");
                    vm.GetStartEndDates(type, e, action);
                }
                else if (type == "Yesterday") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("day");
                    vm.GetStartEndDates("yesterday", e, action);
                }
                else if (type == "Week") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("week");
                    vm.GetStartEndDates("thisweek", e, action);
                }
                else if (type == "LastWeek") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("week");
                    vm.GetStartEndDates("lastweek", e, action);
                }
                else if (type == "Month") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("month");
                    vm.GetStartEndDates("thismonth", e, action);
                }
                else if (type == "LastMonth") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("month");
                    vm.GetStartEndDates("lastmonth", e, action);
                }
                else if (type == "AllTime") {
                    $(e).closest('.date_range').find('#hdnDateSpan').val("day");
                    vm.GetStartEndDates("thisyear", e, action);
                }
            }
            $(e).closest('.date_range').find('#imgNext').show();
            $(e).closest('.date_range').find('#imgPrev').show();
        },
        GetToDoList() {
            var vm = this;
            DataService.gettoDoList('ModuleName=' + vm.moduleName)
                .then(function (response) {
                    vm.encCompanyId = response.data.COMPANY_ID;
                    vm.encUserId = response.data.CREATED_BY;
                    vm.encTrackId = response.data.TRACK_ID;
                })
        },
        GetReports() {
            var vm = this;
            vm.isLoading = true;
            DataService.GetReports('moduleName=' + vm.moduleName)
                .then(function (response) {
                    vm.isLoading = false;
                    vm.widgets = response.data.list;
                    vm.GetToDoList();
                })
        },
        SetDateRange(from, to) {
            this.fromDate = from;
            this.toDate = to;
            this.GetReports();
        },
        MyViews() {
            this.$refs.countList.$data.showTeam = false;
            this.$refs.countList.FetchData('kanban', null);
            this.$refs.todolist.FetchDataKanban('kanban', null);
            this.$refs.countList.users = null;
            this.GetReports();
        },
        TeamView() {          
            this.$refs.countList.$data.showTeam = true;
            this.$refs.countList.FetchData('kanban', null);
        },
        GetToDoList() {
            var vm = this;
            vm.isLoading = true;
            DataService.gettoDoList('ModuleName=' + vm.moduleName)
                .then(function (response) {
                    vm.isLoading = false;
                    console.log(response);
                    vm.encCompanyId = response.data.COMPANY_ID;
                    vm.encUserId = response.data.CREATED_BY;
                    vm.encTrackId = response.data.TRACK_ID;
                })
        },
        ddlChangeView(SelectedVal) {
            if (SelectedVal == "R") {
                $("#divReportsdata, #divAddMoreReports").show();
                $("#divReportsdata").addClass("col-md-12").removeClass("col-md-7").show();
                $("#divReportsdata").find(".graph-widget").addClass("col-md-4").removeClass("col-md-6");
                $("#divToDoList").hide();
            }
            else if (SelectedVal == "T") {
                $("#divToDoList").addClass("col-md-12").removeClass("col-md-5").show();
                $("#divReportsdata").find(".graph-widget").addClass("col-md-6").removeClass("col-md-4").removeClass("col-lg-4").removeClass("col-xl-4");
                $("#divReportsdata, #divAddMoreReports").hide();
            }
            else {
                $("#divToDoList").addClass("col-md-5").removeClass("col-md-12").show();
                $("#divReportsdata").addClass("col-md-7").removeClass("col-md-12").show();
                $("#divReportsdata").find(".graph-widget").addClass("col-md-6").removeClass("col-md-4").removeClass("col-lg-4").removeClass("col-xl-4");
                $("#divReportsdata, #divAddMoreReports").show();
            }
            $("#ddlChangeView .spnChangeView").html($(this).html());
            $('.taskclass').removeClass('col-md-8 col-lg-8').addClass('col-md-4 col-lg-4');
        }
    }
}
</script>
<style>
#chartInTransitAssetList,
.chartInTransitAssetListLegend {
    float: left;
}

.legend span {
    display: inline-block;
    margin-left: 7px;
    margin-right: 7px;
    padding: 5px;
    margin-top: 5px;
}

.chartInTransitAssetListLegend .legned div {
    width: 100%;
}

.chartInTransitAssetListLegend .legend {
    max-height: 328px;
    overflow-y: auto;
}

.legend span {
    display: inline-block;
    margin-left: 7px !important;
    margin-right: 0px !important;
    padding: 5px !important;
    margin-top: 7px !important;
    float: left !important;
    width: 10px !important;
}

.legend span.namespan {
    width: 66% !important;
    float: right !important;
    text-align: left;
    margin: 0px !important;
    padding: 0px !important;
    margin-right: 15px !important;
}
</style>

